<template>
  <vs-dropdown vs-trigger-click class="cursor-pointer" :ref="dropdownRef">
    <small class="flex cursor-pointer">{{ intervalDescription }} <i class="far fa-chevron-down ml-2"></i></small>
    <vs-dropdown-menu class="w-32">
      <vs-dropdown-item @click="pickInterval(1)">{{ $t('PAST_DAY') }}</vs-dropdown-item>
      <vs-dropdown-item @click="pickInterval(7)">{{ $t('PAST_WEEK') }}</vs-dropdown-item>
      <vs-dropdown-item @click="pickInterval(30)">{{ $t('PAST_MONTH') }}</vs-dropdown-item>
      <vs-dropdown-item @click="pickInterval(365)">{{ $t('PAST_YEAR') }}</vs-dropdown-item>
    </vs-dropdown-menu>
  </vs-dropdown>
</template>

<script>
export default {
  name: 'nv-time-interval-dropdown',
  props: {
    dropdownRef: {
      type: String,
      default: 'time-interval-dropdown'
    }
  },
  data: function () {
    return {
      interval: 7
    }
  },
  computed: {
    intervalDescription: function () {
      if (this.interval === 1) {
        return this.$t('PAST_DAY')
      } else if (this.interval === 7) {
        return this.$t('PAST_WEEK')
      } else if (this.interval === 30) {
        return this.$t('PAST_MONTH')
      } else if (this.interval === 365) {
        return this.$t('PAST_YEAR')
      } else {
        return this.$t('CHOOSE_INTERVAL')
      }
    }
  },
  methods: {
    pickInterval: function (interval) {
      this.interval = interval
      this.$emit('timeIntervalChanged', interval)
    }
  }
}
</script>
