import AccountService from '@/services/account/AccountService'
import AlertMixin from '@/mixins/commons/AlertMixin'
import DatePickerMixin from '@/mixins/commons/DatePickerMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import ListLayoutMixin from '@/mixins/commons/ListLayoutMixin'

let AccountTransactionTableMixin = {
  mixins: [AlertMixin, DatePickerMixin, LoadingMixin, ListLayoutMixin],
  data: function () {
    return {
      transactionTableData: [],
      transactionTableContainer: 'loading-container',
      transactionTableBusy: false,
      transactionTableTotalRows: 0,
      transactionTableCurrentPage: 1,
      transactionTablePerPage: 10,
      columnDefs: [{
        headerName: this.$t('POSTING_DATE'),
        field: 'postingDate',
        format: 'Date'
      }, {
        headerName: this.$t('VALUE_DATE'),
        field: 'valueDate',
        format: 'Date'
      }, {
        headerName: this.$t('DESCRIPTION'),
        field: 'narrative'
      }, {
        headerName: this.$t('TRANSACTION_ID'),
        field: 'transactionId'
      }, {
        headerName: this.$t('DEBIT_AMOUNT'),
        field: 'debitAmount',
        format: 'Decimal'
      }, {
        headerName: this.$t('CREDIT_AMOUNT'),
        field: 'creditAmount',
        format: 'Decimal'
      }, {
        headerName: this.$t('BALANCE'),
        field: 'accountBalance',
        format: 'Decimal'
      }]
    }
  },
  computed: {
    transactionTableNoDataText: function () {
      return this.transactionTableBusy ? this.$t('LOADING') + ' ...' : this.$t('NO_DATA_FOUND')
    },
    transactionTableTotalPages: function () {
      return Math.ceil(this.transactionTableTotalRows / this.transactionTablePerPage)
    },
    transactionTablePagingInfo: function () {
      return this.pagingInfo(this.transactionTableCurrentPage, this.transactionTablePerPage, this.transactionTableTotalRows)
    }
  },
  methods: {
    loadTransactionTableData (loadingContainerId, fromDate, toDate) {
      let self = this
      this.transactionTableBusy = true
      this.transactionTableContainer = loadingContainerId || this.transactionTableContainer
      this.startLoading(this.transactionTableContainer)
      AccountService.transactions({
        accountNumber: this.accountNumber,
        fromDate: this.getTime(fromDate || this.fromDate),
        toDate: this.getTime(toDate || this.toDate),
        typeId: this.typeId,
        orderByField: this.orderByField,
        fromAmount: this.fromAmount ? this.fromAmount : null,
        toAmount: this.toAmount ? this.toAmount : null
      }).then(function (response) {
        self.stopLoading(self.transactionTableContainer)
        self.transactionTableBusy = false
        self.transactionTableData = response.data.transactionList || []
        self.transactionTablePerPage = response.data.totalRows
        self.transactionTableTotalRows = response.data.totalRows

        for (let i = 0; i < self.transactionTableData.length; i++) {
          let trx = self.transactionTableData[i]
          self.transactionTableData[i]['debitAmount'] = trx.drCrIndicator === 'D' ? trx.amount : ''
          self.transactionTableData[i]['creditAmount'] = trx.drCrIndicator === 'C' ? trx.amount : ''
        }
      }).catch(function (error) {
        self.stopLoading(self.transactionTableContainer)
        self.transactionTableBusy = false
        self.showDangerAlert(error.response.data.errors)
      })
    },
    refreshTransactionTable () {
      this.loadTransactionTableData()
    }
  },
  watch: {
    transactionTableCurrentPage: function () {
      this.refreshTransactionTable()
    }
  }
}

export default AccountTransactionTableMixin
