let DatePickerMixin = {
  data: function () {
    return {
      dateFormat: 'yyyy-MM-dd'
    }
  },
  methods: {
    chooseInterval (interval) {
      let date = new Date()
      switch (interval) {
        case 'day':
          date.setDate(date.getDate() - 1)
          break
        case 'week':
          date.setDate(date.getDate() - 7)
          break
        case 'month':
          date.setDate(date.getDate() - 30)
          break
        case 'year':
          date.setDate(date.getDate() - 365)
          break
        default:
      }
      this.fromDate = date
    },
    getTime (date) {
      if (date instanceof Date) {
        return date.getTime()
      } else if (!isNaN(date)) {
        return date
      } else {
        return null
      }
    }
  }
}

export default DatePickerMixin
