<template>
  <div>
    <div class="alert-container" v-if="alertActive">
      <vs-alert :active.sync="alertActive" :color="alertVariant" closable icon-pack="feather" close-icon="icon-x" class="mb-5">
        <p>{{ alertMessage }}</p>
      </vs-alert>
    </div>

    <nv-card class="mb-base">
      <div class="flex flex-wrap-reverse items-center mb-base">
        <vs-button class="p-3 mr-4" @click="cancel" color="primary" type="filled" icon="fa-chevron-left" icon-pack="far">{{ $t('BACK') }}</vs-button>
      </div>
      <div id="acct-loading-container" class="nv-row row hover vs-con-loading__container">
        <div class="nv-col md:w-1/2 w-full">
          <p class="info-row">
            <span>{{ $t('ACCOUNT_TITLE') }}:</span>
            <span>{{ account.title | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('ACCOUNT_NUMBER') }}:</span>
            <span>{{ account.accountNumber | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('ACCOUNT_TYPE') }}:</span>
            <span>{{ account.typeName | placeholder('-') }}</span>
          </p>
          <p class="info-row" v-if="account.statusName">
            <span>{{ $t('STATUS') }}:</span>
            <span>{{ account.statusName | placeholder('-') }}</span>
          </p>
          <p class="info-row" v-if="account.nickname">
            <span>{{ $t('NICKNAME') }}:</span>
            <span>{{ account.nickname | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('IBAN') }}:</span>
            <span>{{ account.iban | placeholder('-') }}</span>
          </p>
        </div>

        <div class="nv-col md:w-1/2 w-full">
          <p class="info-row">
            <span>{{ $t('CURRENCY') }}:</span>
            <span>{{ account.currencyAbbreviation | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('AVAILABLE_BALANCE') }}:</span>
            <span :class="negativeNumberStyle(account.availableBalance)">{{ account.availableBalance | currency('') | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('WORKING_BALANCE') }}:</span>
            <span :class="negativeNumberStyle(account.workingBalance)">{{ account.workingBalance | currency('') | placeholder('-') }}</span>
          </p>
          <p class="info-row">
            <span>{{ $t('LEDGER_BALANCE') }}:</span>
            <span :class="negativeNumberStyle(account.ledgerBalance)">{{ account.ledgerBalance | currency('') | placeholder('-') }}</span>
          </p>
          <p class="info-row" v-if="account.overdraftLimit">
            <span>{{ $t('OVERDRAFT_LIMIT') }}:</span>
            <span :class="negativeNumberStyle(account.overdraftLimit)">{{ account.overdraftLimit | currency('') | placeholder('-') }}</span>
          </p>
        </div>
      </div>
    </nv-card>

    <div v-if="transactionTableData">
      <nv-card id="trx-loading-container" class="vs-con-loading__container">
        <div class="mb-base flex justify-between">
          <h3>{{ $t('RECENT_TRANSACTIONS') }}</h3>
          <div class="text-right">
            <nv-time-interval-dropdown v-on:timeIntervalChanged="timeIntervalChanged"/>
          </div>
        </div>

        <vs-table :data="transactionTableData" :noDataText="transactionTableNoDataText" search>
          <div slot="header" class="flex flex-wrap-reverse items-center flex-grow justify-between">
            <div class="flex flex-wrap-reverse items-center">
              <vs-button class="p-3 mr-4" @click="exportXls(transactionTableData, columnDefs)" type="filled" icon="fa-file-excel" icon-pack="fal">{{ $t('EXPORT_XLS') }}</vs-button>
              <vs-button class="p-3 mr-4" @click="exportCsv(transactionTableData, columnDefs)" type="filled" icon="fa-file-csv" icon-pack="fal">{{ $t('EXPORT_CSV') }}</vs-button>
              <vs-button class="p-3 mr-4" @click="refreshRecentTransactionTableData" type="filled" icon="fa-sync" icon-pack="far" :disabled="transactionTableBusy">{{ $t('REFRESH') }}</vs-button>
            </div>

            <div class="flex justify-end mr-4">
              <div class="p-2 border border-solid border-grey-light rounded-full d-theme-dark-bg flex items-center justify-between font-medium">
                <span class="mx-2">{{ transactionTablePagingInfo }}</span>
              </div>
            </div>
          </div>

          <template slot="thead">
            <vs-th sort-key="postingDate">
              {{ this.$t('POSTING_DATE') }}
            </vs-th>
            <vs-th sort-key="valueDate">
              {{ this.$t('VALUE_DATE') }}
            </vs-th>
            <vs-th sort-key="description">
              {{ this.$t('TRANSACTION_DESCRIPTION') }}
            </vs-th>
            <vs-th sort-key="transactionId">
              {{ this.$t('TRANSACTION_ID') }}
            </vs-th>
            <vs-th sort-key="amount">
              {{ this.$t('DEBIT_AMOUNT') }}
            </vs-th>
            <vs-th sort-key="amount">
              {{ this.$t('CREDIT_AMOUNT') }}
            </vs-th>
            <vs-th sort-key="accountBalance">
              {{ this.$t('BALANCE') }}
            </vs-th>
          </template>

          <template slot-scope="{data}">
            <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
              <vs-td :data="data[index].postingDate">
                {{ data[index].postingDate | formatUnixDate | placeholder('-') }}
              </vs-td>
              <vs-td :data="data[index].valueDate">
                {{ data[index].valueDate | formatUnixDate | placeholder('-') }}
              </vs-td>
              <vs-td :data="data[index].narrative">
                {{ data[index].narrative | placeholder('-') }}
              </vs-td>
              <vs-td :data="data[index].transactionId">
                {{ data[index].transactionId | placeholder('-') }}
              </vs-td>
              <vs-td :data="data[index].amount">
                <span :class="positiveNumberStyle(data[index].amount)">{{ data[index].drCrIndicator === 'D' ? data[index].amount : '' | currency('') | placeholder('-') }}</span>
              </vs-td>
              <vs-td :data="data[index].amount">
                <span :class="negativeNumberStyle(data[index].amount)">{{ data[index].drCrIndicator === 'C' ? data[index].amount : '' | currency('') | placeholder('-') }}</span>
              </vs-td>
              <vs-td :data="data[index].accountBalance">
                <span :class="negativeNumberStyle(data[index].accountBalance)">{{ data[index].accountBalance | currency('') | placeholder('-') }}</span>
              </vs-td>
            </vs-tr>
          </template>
        </vs-table>
      </nv-card>
    </div>
  </div>
</template>

<script>
import NvTimeIntervalDropdown from '@/components/nv-time-interval-dropdown/NvTimeIntervalDropdown'
import AlertMixin from '@/mixins/commons/AlertMixin'
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import AccountTransactionTableMixin from '@/mixins/account/AccountTransactionTableMixin'
import NumberStyleMixin from '@/mixins/commons/NumberStyleMixin'
import AccountService from '@/services/account/AccountService'

export default {
  name: 'account-view',
  mixins: [AccountTransactionTableMixin, AlertMixin, LoadingMixin, NumberStyleMixin],
  components: {
    NvTimeIntervalDropdown
  },
  data: function () {
    return {
      account: {},
      transactionFromDate: null,
      transactionToDate: null
    }
  },
  methods: {
    async load () {
      let self = this
      this.startLoading('acct-loading-container')
      AccountService.accounts({
        accountNumber: this.$route.params.id
      }).then(function (response) {
        self.stopLoading('acct-loading-container')
        self.account = response.data.accountList[0] || {}
      }).catch(function (error) {
        self.stopLoading('acct-loading-container')
        self.showDangerAlert(error.response.data.errors)
      })
    },
    loadRecentTransactionTableData () {
      let today = new Date()
      this.accountNumber = this.$route.params.id
      this.transactionFromDate = new Date().setDate(today.getDate() - 7)
      this.transactionToDate = today
      this.loadTransactionTableData('trx-loading-container', this.transactionFromDate, this.transactionToDate)
    },
    refreshRecentTransactionTableData () {
      this.loadTransactionTableData('trx-loading-container', this.transactionFromDate, this.transactionToDate)
    },
    timeIntervalChanged (interval) {
      let today = new Date()
      this.accountNumber = this.$route.params.id
      this.transactionFromDate = new Date().setDate(today.getDate() - interval)
      this.transactionToDate = today
      this.loadTransactionTableData('trx-loading-container', this.transactionFromDate, this.transactionToDate)
    },
    cancel () {
      this.$router.back()
    }
  },
  mounted () {
    this.load()
    this.loadRecentTransactionTableData()
  }
}
</script>
